import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { Response } from '@angular/http';
import { AuthService } from '../../services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  isLoggedIn:boolean;
  loggedInUser:string;
  
  constructor(private authService: AuthService, private router: Router, private route: ActivatedRoute, private flash: FlashMessagesService) { }

  ngOnInit() {
    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.isLoggedIn=true;
        this.loggedInUser = auth.displayName;  
      }
    })
  }
  onLogoutClick(){
    this.isLoggedIn=false;
    this.authService.logout();
    this.flash.show('You are now logged out',{cssClass:'alert-success', timeout:4000});
    this.router.navigate(['/login'])
  }
}
