import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Recipe } from '../models/recipe.model';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  recipesCollection: AngularFirestoreCollection<Recipe>;
  recipeDoc: AngularFirestoreDocument<Recipe>;
  recipes: Observable<Recipe[]>;
  recipe: Observable<Recipe>;
  userId:string;
  username:string;

  constructor(private db: AngularFirestore, private authService:AuthService) { 
    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.userId=auth.uid;
        this.recipesCollection = this.db.collection('recipes', ref => ref.where('userId', '==', this.userId));
      }
    })
  }
  getUser(uid){
    const user= this.db.collection('users', ref => ref.where('userId', '==', uid)).valueChanges()
    this.username = 'Naam';
    return user;
  }
  deleteRecipe(id:string){
    this.recipeDoc=this.db.doc(`recipes/${id}`);
    this.recipeDoc.delete();
  }

  addNewRecipe(recipe:Recipe){
    recipe.userId=this.userId;
    recipe.dateAdded=new Date();
    this.recipesCollection.add(recipe);
  }
  updateRecipe(id:string, recipe:Recipe){
    this.recipeDoc=this.db.doc(`recipes/${id}`)
    this.recipeDoc.update(recipe);
  }

  getRecipe(id:string){
    return this.db.doc(`recipes/${id}`).valueChanges();
  }

  getAllRecipes(){
    return this.recipesCollection.snapshotChanges().pipe(
      map(actions => {       
        return actions.map(a => {
          const data = a.payload.doc.data() as Recipe;
          
          data.id = a.payload.doc.id;
          // console.log(this.getUser(data.userId))
          this.getUser(data.userId).subscribe(user=>{data.username = this.username})
          // sub.unsubscribe();
          return data;
        });
      })
    )
  }

  getUserRecipes(){
    return this.db.collection('recipes').snapshotChanges().pipe(
      map(actions => {       
        return actions.map(a => {
          const data = a.payload.doc.data() as Recipe;
          data.id = a.payload.doc.id;
          return data;
        });
      })
    )
  }
  
}
