import { Component, OnInit, OnDestroy } from '@angular/core';
import { DataService } from '../../services/data.service';
import { Recipe } from '../../models/recipe.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { resolve } from 'q';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {
  subscription;
  recipes:Recipe[];
  selectedRecipe:Recipe={imagePath:'', ingredients:[]};
  constructor(private dataService:DataService, private modalService:NgbModal) { }

  ngOnInit() {
    this.subscription=this.dataService.getAllRecipes().subscribe(recipes=>{this.recipes = recipes});
    
  }
  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

  onClickOpenModal(id, content){
    this.dataService.getRecipe(id).subscribe(recipe=>{this.selectedRecipe = recipe});
    this.modalService.open(content,{ size: 'lg' });
  }

}
