import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ShoppingService } from '../../../services/shopping.service';
import { Ingredient } from '../../../models/ingredient.model';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css']
})
export class ShoppingEditComponent implements OnInit, OnDestroy {
  @ViewChild('f') shopForm:NgForm;
  sub: Subscription;
  editMode = false;
  editId:number;
  editItem: Ingredient;

  constructor(private shoppingService:ShoppingService){}

  onAdd(form: NgForm){
    if(this.editMode){
      this.shoppingService.updateIngredient(this.editId, new Ingredient(form.value.name, form.value.amount));
    }else{
      this.shoppingService.addIngredient(new Ingredient(form.value.name, form.value.amount));
    }    
    this.onClear();    
  }
  onDelete(){
    this.shoppingService.deleteIngredient(this.editId);
    this.onClear();
  }
  onClear(){
    this.shopForm.reset();
    this.editMode=false;
  }

  ngOnInit() {
    this.sub = this.shoppingService.startEdit.subscribe((index:number)=>{
      this.editMode=true;
      this.editId=index;
      this.editItem = this.shoppingService.getIngredient(index);
      this.shopForm.setValue({
        name: this.editItem.name,
        amount: this.editItem.amount
      })
    });
  }
  ngOnDestroy(){
    this.sub.unsubscribe();
  }
}
