import { Component, OnInit} from '@angular/core';
import { Recipe } from '../../../models/recipe.model';
import { Ingredient } from '../../../models/ingredient.model';
import { RecipeService } from '../../../services/recipe.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { DataService } from '../../../services/data.service';

@Component({
  selector: 'app-recipe-detail',
  templateUrl: './recipe-detail.component.html',
  styleUrls: ['./recipe-detail.component.css']
})
export class RecipeDetailComponent implements OnInit {
  recipe: Recipe = {name:'',description:'',id:'', ingredients:[],imagePath:''};
  id: string;

  constructor(private recipeService:RecipeService, private route: ActivatedRoute, private router:Router, private dataService:DataService) { }

  addToList(){
    this.recipeService.addToList(this.recipe.ingredients)
  }
  ngOnInit() {
    this.route.params
      .subscribe((params:Params)=>{ 
        this.id = params['id'];
        // this.recipe = this.recipeService.getRecipe(this.id);
        this.dataService.getRecipe(this.id).subscribe(recipe=>{this.recipe = recipe});
      });
    
  }
  onEdit(){
    this.router.navigate(['edit'], {relativeTo:this.route} );
  }
  onDelete(){
    // this.recipeService.deleteRecipe(this.id);
    this.dataService.deleteRecipe(this.id);
    
    this.router.navigate(['../'], {relativeTo: this.route});
  }

}
