import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit { 
  userForm = new FormGroup({
    'name': new FormControl(null, Validators.required),
    'email': new FormControl(null, [Validators.required, Validators.email]),
    'password': new FormControl(null, Validators.required)
  });
  constructor(private authService:AuthService, private flash: FlashMessagesService, private router:Router) { }

  ngOnInit() {
    this.authService.getAuth().subscribe(auth=>{
      if(auth){
        this.router.navigate(['/']);
      }
    })
  }

  onSubmit(){
    console.log(this.userForm.value)
    this.authService.register(this.userForm.value.email, this.userForm.value.password, this.userForm.value.name).then(res=>{
        this.flash.show('You are registered and logged in',{cssClass:'alert-success', timeout:4000});
      this.router.navigate(['/']).catch(err=>{
        this.flash.show(err.message,{cssClass:'alert-danger', timeout:4000});
      });
    })
  }

}
