import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { RecipeService } from '../../../services/recipe.service';
import { DataService } from '../../../services/data.service';
import { Recipe } from '../../../models/recipe.model';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styleUrls: ['./recipe-edit.component.css']
})
export class RecipeEditComponent implements OnInit {
  id:string;
  userId:string;
  editMode = false;
  recipeForm: FormGroup;
  recipe: Recipe = {userId:'',name:'',description:'',id:'', ingredients:[],imagePath:''};

  constructor(private route:ActivatedRoute, private router: Router, private dataService:DataService) { }

  ngOnInit() {
    this.route.params.subscribe(
      (params:Params)=>{
        this.id = params['id'];
        this.editMode = params['id'] !=null;
        this.initForm();
        if(this.editMode){
          this.dataService.getRecipe(this.id).subscribe(recipe=>{this.recipe = recipe, this.initForm()});
        }
      }
    );
  }
  private initForm(){
    let recipeName='';
    let recipeImg='';
    let recipeDesc='';
    let recipeIngredients = new FormArray([]);

    if(this.editMode){      
      recipeName = this.recipe.name;
      recipeImg= this.recipe.imagePath;
      recipeDesc= this.recipe.description;
      if(this.recipe['ingredients']){
        for(let ingredient of this.recipe.ingredients){
          recipeIngredients.push(
            new FormGroup({
              'name': new FormControl(ingredient.name, Validators.required),
              'amount': new FormControl(ingredient.amount, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
            })
          )
        }        
      }
    }

    this.recipeForm = new FormGroup({
      'name': new FormControl(recipeName, Validators.required),
      'imagePath': new FormControl(recipeImg, Validators.required),
      'description': new FormControl(recipeDesc, Validators.required),
      'ingredients': recipeIngredients
    });
  }
  addNewRecipe(){
    (<FormArray>this.recipeForm.get('ingredients')).push(
      new FormGroup({
        'name': new FormControl(null,  Validators.required),
        'amount': new FormControl(null, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
      })
    );
  }
  onSubmit(){
    // const newRecipe = new Recipe(this.recipeForm.value['name'],this.recipeForm.value['description'],this.recipeForm.value['imagePath'],this.recipeForm.value['ingredients']);
    if(this.editMode){
      // this.recipeService.updateRecipe(this.id, this.recipeForm.value);
      this.dataService.updateRecipe(this.id, this.recipeForm.value);
    }else{
      // this.recipeService.addRecipe(this.recipeForm.value);
      this.dataService.addNewRecipe(this.recipeForm.value);
      
    }
    this.router.navigate(['../'], {relativeTo: this.route});
  }
  onDeleteIngredient(index:number){
    (<FormArray>this.recipeForm.get('ingredients')).removeAt(index);
  }
  onCancel(){
    this.router.navigate(['../'], {relativeTo: this.route});
  }
}
